import $ from '../lib/zepto'

let apiBaseUrl = 'http://localhost:8081'
let headers = {}

export default {
  /**
   * Fetch all tasks
   */
  fetchAllTasks: () => {
    return $.ajax({
      type: 'GET',
      headers: headers,
      cache: false,
      url: apiBaseUrl + '/api/tasks',
      dataType: 'json'
    })
  },

  /**
   * Fetch tasks of this host
   */
  fetchTasks: (uuid) => {
    return $.ajax({
      type: 'GET',
      headers: headers,
      cache: false,
      url: apiBaseUrl + '/api/tasks/' + uuid,
      dataType: 'json'
    })
  },

  fetchLogs: (mac) => {
    return $.ajax({
      type: 'GET',
      headers: headers,
      cache: false,
      url: apiBaseUrl + '/analytics/logs?mac=' + mac
    })
  }
}



