<template>
  <div class="log">
    <div class="header">
      <h1>Log</h1>
      <h2>App logs.</h2>
    </div>

    <pre>
      {{ logs }}
    </pre>
  </div>

</template>


<script>
import server from '../model'
import $ from '../lib/zepto'

export default {
  name: 'Log',
  el: () => ('#log'),
  data: () => ({
    logs: null
  }),
  methods: {
    fetchData () {
      console.log('will fetch data')
      server.fetchLogs('D0:49:00:00:C5:54')
      .done((data) => {
        this.logs = data.content
      })
      .fail(() => {
        console.log('Fetch data failed.')
      })
    }
  },
  ready: () => {
    $(document).on('viewDidAppear', () => {
      console.log('ready')
      this.fetchData()
    })
  }
}
</script>


